<template>
  <div class="align-items-center flex justify-content-center align-center">
    <ProgressSpinner />
  </div>
</template>

<script>
import ProgressSpinner from "primevue/progressspinner";
import { useStore } from "vuex";
import { onMounted, reactive, ref } from "@vue/runtime-core";
import { degrees, PDFDocument, rgb, StandardFonts } from "pdf-lib";
import { useRouter } from "vue-router";
export default {
  components: {
    ProgressSpinner,
  },
  setup() {
    let store = useStore();
    const router = useRouter();
    let color = ref("");
    let score = ref(null);
    const userObj = reactive({
      email: "",
      company: "",
      name: "",
    });
    let link = [
      "https://raw.githubusercontent.com/dmrt2002/images/696ad09814061b0a6d1ff1653ac680969870b760/heptagon.svg",
    ];
    score.value = store.getters.getScore;
    color.value = "Blue";
    const savePdf = async () => {
      const url = "https://hept-data.herokuapp.com/Data_Explorer.pdf";
      const imageUrl =
        "data:image/png;base64,";
      const existingPdfBytes = await fetch(url).then((res) =>
        res.arrayBuffer()
      );
      const pngImageBytes = await fetch(imageUrl).then((res) =>
        res.arrayBuffer()
      );
      const pdfDoc = await PDFDocument.load(existingPdfBytes);
      const helveticaFont = await pdfDoc.embedFont(StandardFonts.Helvetica);
      const pngImage = await pdfDoc.embedPng(pngImageBytes);
      const pages = pdfDoc.getPages();
      const firstPage = pages[0];
      const { width, height } = firstPage.getSize();
      firstPage.drawText(userObj.name, {
        x: width / 10,
        y: height - height / 9,
        size: 36,
        font: helveticaFont,
        color: rgb(0.953, 0.954, 0.955),
        rotate: degrees(0),
      });
      firstPage.drawText(userObj.email, {
        x: width / 10,
        y: height - height / 7.25,
        size: 32,
        font: helveticaFont,
        color: rgb(0.953, 0.954, 0.955),
        rotate: degrees(0),
      });
      firstPage.drawText(userObj.company, {
        x: width / 10 ,
        y: height - height / 6.2,
        size: 28,
        font: helveticaFont,
        color: rgb(0.953, 0.954, 0.955),
        rotate: degrees(0),
      });
      firstPage.drawImage(pngImage, {
        x: width/12.5 + width * 1/124.5 * score.value,
        y: height - height / 3.65,
        width: 40,
        height: 40,
      });
      firstPage.drawText("You are Here", {
        x: width/12.5 + width * 1/124.5 * score.value - 0.10 * width,
        y: height - height / 3.8,
        size: 18,
        font: helveticaFont,
        color: rgb(0, 0, 1),
        rotate: degrees(0),
      });
      let num = score.value;
      let text = num.toString();
      firstPage.drawText(text, {
        x: width / 1.48,
        y: height - height / 6.65,
        size: 55,
        font: helveticaFont,
        color: rgb(0, 0, 1),
        rotate: degrees(0),
      });
      const pdfBytes = await pdfDoc.save();
      require("downloadjs")(pdfBytes, "Results.pdf", "application/pdf");
      router.go(-1)
    };
    onMounted(async () => {
      let user = store.getters.getUserObj;
      userObj.email = user.email;
      userObj.company = user.company;
      userObj.name = user.name;
      savePdf();
    });
    return { link, savePdf, userObj, score, color };
  },
};
</script>

<style scoped>
.align-center {
  height: 99vh;
}
</style>
